import React, { Component } from 'react';
import './App.css'; 
import RoutePage from './Routing.js'; 
import LoginPage from './LoginPage'; 
import LoginFunction from './LoginFunction';
class App extends Component { 
  
  constructor(props){ 
    super(props); 
    this.state = { 
        currentuserid:null, 
        loggedIn:false, 
        currentUsername:null
              
    }
  }  

  setCurrentUser=(userid, enteredUsername, loginAccepted)=>{ 
    this.setState({currentuserid:userid}); 
    this.setState({loggedIn:loginAccepted}); 
    this.setState({currentUsername:enteredUsername})
  }

  render(){  
    if(!this.state.loggedIn){ 
      return( 
        <LoginPage setCurrentUser={this.setCurrentUser}/>
      );
    } 
    else{
    return(
    <div> 
      <h1>Good Job you be logged in as {this.state.currentUsername}</h1>
      {/* <RoutePage></RoutePage>  */}
   
    </div>
    );  
    }
  }
}

export default App;
